body {
  margin: 0;
}
.lightMode {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: #fff;
  color: #333;
  transition: background-color 0.4s ease-out;
}

.darkMode {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: #1a1919;
  color: #999;
  transition: background-color 0.4s ease-out;
}

.image {
  width: 350px;
  margin: 50px 0;
}

/*Toggle Container Styles*/
.toggle {
  position: relative;
  padding: 0 4px;
  display: flex;
  align-items: center;
}

input[type='checkbox'].checkbox {
  width: 40px;
  height: 10px;
  background: #555;
  position: relative;
  border-radius: 5px !important;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  cursor: pointer;
  vertical-align: 2px;
  outline: none;
}

input[type='checkbox'].checkbox:checked + label {
  left: 30px;
}

input[type='checkbox'].checkbox:focus-visible {
  outline: solid 2px white;
}

input[type='checkbox'].checkbox + label {
  display: inline-block;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  transition: all 0.3s ease;
  cursor: pointer;
  position: absolute;
  left: 2px;
  background: #fff;
  opacity: 0.9;
  background-color: cyan;
}

.toggleContainer {
  display: flex;
  margin-bottom: 3rem;
}

.toggleContainer > span {
  font-size: 1.2em;
  background: none;
  border: none;
  cursor: pointer;
  transition: color 0.3s ease;
  margin-top: 4px;
  margin-right: 2px;
}

.toggleContainer > span:last-child {
  margin-top: 5px;
}

@media (max-width: 768px) {
  .container {
    margin: 0 10%;
  }

  .image {
    width: 100%;
  }
}
